<template>
  <div class="section">
    <div class="columns">
      <div class="column is-three-fifths is-offset-one-fifth">
        <h2 class="title is-3"></h2>
        <div class="notification">
          <a href="https://twitter.com/mDocSupportTeam" target="_blank">Twitter</a><br>
          <br>
          
        </div>
        <b-field>
          <b-input maxlength="200" type="textarea" v-model="text"></b-input>
        </b-field>
        <button class="button is-fullwidth" :disabled="disabled" @click="onSubmit"></button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import NotificationMixin from '@/mixins/NotificationMixin';

const ContactForm = {
  data() {
    return { text: '' };
  },
  computed: {
    disabled() {
      return this.text.length === 0;
    },
  },
  methods: {
    onSubmit() {
      if (this.disabled) { return; }

      // eslint-disable-next-line no-alert
      if (window.confirm('')) {
        axios.post('/contact/', {
          text: this.text,
        })
          .then(() => {
            this.text = '';
            this.notifySuccess('');
          })
          .catch(() => {
            this.notifyError('');
          });
      }
    },
  },
  mixins: [NotificationMixin],
};

export default ContactForm;
</script>
